code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes fadeInOut {
  0%{
    transform: translateX(-50%) translateY(calc(-20px - 100%));
    opacity: 0;
  }
  7%{
    transform: translateX(-50%) translateY(0%);
    opacity: 1;
  }
  93%{
    transform: translateX(-50%) translateY(0%);
    opacity: 1;
  }
  100%{
    transform: translateX(-50%) translateY(calc(20px + 100%));
    opacity: 0;
  }
}

.error-pod p{
  margin: 0;
  background-color: #121212;
  width: 100%;
  height: 100%;
  padding: 10px 20px;
  border-radius: 7px;
  box-sizing: border-box;
  color: rgb(225, 32, 32);
}
.error-pod{
  animation: fadeInOut 5s;
  position: absolute;
  top: 20px;
  padding: 1px 1px 1px 10px;
  border-radius: 8px;
  left: 50%;
  transform: translateX(-50%) translateY(calc(20px + 100%));
  opacity: 0;
  z-index: 11;
  background-color: rgb(203, 27, 27);
}


@keyframes dot {
  0%{
    opacity: 0;
  }
  20.9%{
    opacity: 0;
  }
  50%{
    opacity: 1;
  }
  80%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}

.dot-holder > .dot.one{
  animation: infinite dot 1.5s;
  color: #1DB954;
}
.dot-holder > .dot.two{
  animation: infinite dot 1.5s;
  animation-delay: 0.15s;
  color: #1DB954;
}
.dot-holder > .dot.three{
  animation: infinite dot 1.5s;
  animation-delay: 0.3s;
  color: #1DB954;
}

@keyframes loading{
  0%{
    rotate: 0deg;
  }
  100%{
    rotate: 360deg;
  }
}

.loading{
  animation: infinite loading 1s;
  filter: invert(94%);
}